'use strict';

import React, { Component } from 'react';
import { connect } from 'react-redux';

import { addStudentThunk } from '../reducers/students';
import Header from './Header'

class StudentAdd extends Component {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit (event) {
    //console.log('student event.target', event.target)
    event.preventDefault();
    const student = {
      firstName: event.target.studentFirstName.value,
      lastName: event.target.studentLastName.value,
      campusId: 1,
      imageUrl: event.target.studentImageUrl.value,
      email: event.target.studentEmail.value,
      gpa: event.target.studentGpa.value,
    };
  //  console.log('student before add', student)
    this.props.addStudent(student);
    this.props.history.push('/students');
  }

  render () {
    // broken code for selecting a campus
    console.log('studentadd this.props', this.props)
     const { campuses } = this.props;
    const campusSelect = (<select className="form-control" name="studentCampus">
        {campuses.map(campus => (
           <option key={campus.id} value={campus.id}>
             {campus.name}
           </option>
         ))}
       </select>);

    return (
      <div className="container-add-pages">
          <Header />
      <div>
          <div className="heading">
            <p></p>
            <h3 className="panel-title">New Student</h3>

          </div>

          <div className="body">
            <div className="update">
              <div>
                <form onSubmit={this.handleSubmit}>
                  <div>
                    <label>First Name</label>
                    <input
                      className="form-control"
                      type="text"
                      name="studentFirstName"
                      placeholder="Enter First Name" />
                    <label>Last Name</label>
                    <input
                      className="form-control"
                      type="text"
                      name="studentLastName"
                      placeholder="Enter Last Name" />
                    <label>Image URL</label>
                    <input
                      className="form-control"
                      type="text"
                      name="studentImageUrl"
                      defaultValue="/img/avatar.png" />
                    <label>Email</label>
                    <input
                      className="form-control"
                      type="text"
                      name="studentEmail"
                      placeholder="Enter E-mail" />
                    <label>GPA</label>
                    <input
                      className="form-control"
                      type="text"
                      name="studentGpa"
                      placeholder="Enter GPA" />
                  </div>
                  <div className="form-group">
                    <button type="submit" className="btn">
                      Submit
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  campuses: state.campuses
});

const mapDispatchToProps = (dispatch, props) => {
  let { history } = props;
  return {
    addStudent: student => {
      dispatch(addStudentThunk(student, history));
    }
  };
};

const connector = connect(mapStateToProps, mapDispatchToProps);
export default connector(StudentAdd);
