import axios from 'axios';

// Action Types

const LOAD_CAMPUS = 'LOAD_CAMPUS';
const ADD_CAMPUS = 'ADD_CAMPUS';
const DEL_CAMPUS = 'DEL_CAMPUS';
const UPDATE_CAMPUS = 'UPDATE_CAMPUS';
const ADD_STUDENT_TO_CAMPUS = 'ADD_STUDENT_TO_CAMPUS';
const DEL_STUDENT_FROM_CAMPUS = 'DEL_STUDENT_FROM_CAMPUS';

// Action Creators

const loadCampus = campuses => ({
  type: LOAD_CAMPUS,
  campuses
});

const addCampus = campus => ({
  type: ADD_CAMPUS,
  campus
});

const delCampus = campusId => ({
  type: DEL_CAMPUS,
  campusId
});

const updateCampus = campus => ({
  type: UPDATE_CAMPUS,
  campus
});

export const addStudentToCampus = (campus, student) => ({
  type: ADD_STUDENT_TO_CAMPUS,
  campus,
  student
});

export const delStudentFromCampus = (campus, student) => ({
  type: DEL_STUDENT_FROM_CAMPUS,
  campus,
  student
});

// Campuses Reducer

export default function reducer (campuses = [], action) {
  switch (action.type) {

    case LOAD_CAMPUS:
      return action.campuses;

    case ADD_CAMPUS:
      return [...campuses, action.campus];

    case DEL_CAMPUS:
      return campuses.filter(campus => (campus.id !== action.campusId));

    case UPDATE_CAMPUS:
      return campuses.map(campus => {
        if (campus.id === action.campus.id) {
          action.campus.students = campus.students;
          return action.campus;
        } else {
          return campus;
        }
      });

    case ADD_STUDENT_TO_CAMPUS:
      return campuses.map(campus => {
        if (campus.id === action.campus.id) {
          if (campus.students) campus.students = [...campus.students, action.student];
          else campus.students = [ action.student ];
          return campus;
        } else {
          return campus;
        }
      });

    case DEL_STUDENT_FROM_CAMPUS:
      return campuses.map(campus => {
        if (campus.id === action.campus.id) {
          if (campus.students) {
            campus.students = campus.students.filter(student => {
              return student.id !== action.student.id;
            });
          }
          return campus;
        } else {
          return campus;
        }
      });

    default:
      return campuses;
  }
}

// Thunk Creators

export const getCampuses = () => {
  return dispatch => {
    axios.get('/api/campuses')
      .then(res => res.data)
      .then(campuses => dispatch(loadCampus(campuses)))
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const addCampusThunk = (campus, history) => {
  console.log('the campus within addcampusthunk', campus)
  return dispatch => {
    axios.post('/api/campuses', campus)
      .then(res => res.data)
      .then(createdCampus => {
        dispatch(addCampus(createdCampus));
        history.push(`/`);
      })
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const delCampusThunk = (campusId, history) => {
  return dispatch => {
    axios.delete(`/api/campuses/${campusId}`)
      .then(() => {
        dispatch(delCampus(campusId));
        history.push('/campuses/');
      })
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const updateCampusThunk = (campus, history) => {
  return dispatch => {
    axios.put(`/api/campuses/${campus.id}`, campus)
      .then(res => res.data)
      .then(updatedCampus => {
        dispatch(updateCampus(updatedCampus));
        history.push(`/campuses/${updatedCampus.id}`);
      })
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};
