import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { delStudentThunk } from '../reducers/students';

export class Student extends Component {
  constructor(props) {
    super(props);

    this.handleRemove = this.handleRemove.bind(this);
  }

  handleRemove (event) {
    event.stopPropagation();
    this.props.delStudent(this.props.student);
  }

  render () {
    const { student, campus } = this.props;

    return (
        <div className="student"><main>        <img className='studentImage' width="25%" src={student.imageUrl} /><p></p><NavLink
                to={`/students/${student.id}`}
                className="btn"
                role="button">
               <h3>{student.firstName } {student.lastName}
               </h3></NavLink>           <button
            onClick={this.handleRemove}
            className="btn-remove">
            Delete
          </button>
        <span>{campus ? campus.name : null}</span>
        <div>
      </div>
      </main>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch, test) => {
  let { history } = test;
  return {
    delStudent: studentId => {
      dispatch(delStudentThunk(studentId, history));
    }
  };
};

const connector = connect(null, mapDispatchToProps);
export default connector(Student);
