import axios from 'axios';
import { addStudentToCampus, delStudentFromCampus } from './campuses';

// Action Types

const LOAD_STUDENT = 'LOAD_STUDENT';
const ADD_STUDENT = 'ADD_STUDENT';
const DEL_STUDENT = 'DEL_STUDENT';
const UPDATE_STUDENT = 'UPDATE_STUDENT';

// Action Creators

const loadStudent = students => ({
  type: LOAD_STUDENT,
  students
});

const addStudent = student => ({
  type: ADD_STUDENT,
  student
});

const delStudent = studentId => ({
  type: DEL_STUDENT,
  studentId
});

const updateStudent = student => ({
  type: UPDATE_STUDENT,
  student
});

// Students Reducer

export default function reducer (students = [], action) {
  switch (action.type) {
    case LOAD_STUDENT:
      return action.students;

    case ADD_STUDENT:
      return [...students, action.student];

    case DEL_STUDENT:
      return students.filter(student => (student.id !== action.studentId));

    case UPDATE_STUDENT:
      return students.map(student => {
        if (student.id === action.student.id) {
          action.student.campuses = student.campuses;
            return action.student;
          } else {
            return student;
          }
      });

    default:
      return students;
  }
}

// Thunk Creators

export const getStudents = () => {
  return dispatch => {
    axios.get('/api/students')
      .then(res => res.data)
      .then(students => dispatch(loadStudent(students)))
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const addStudentThunk = (studentData, history) => {
  return dispatch => {
    axios.post('/api/students', studentData)
      .then(res => res.data)
      .then(({ campus, student }) => {
        student.campuses = [ campus ];
        dispatch(addStudent(student));
        dispatch(addStudentToCampus(campus, student));
        history.push(`/students/`);
      })
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const delStudentThunk = (student, history) => {
  return dispatch => {
    axios.delete(`/api/students/${student.id}`)
      .then(() => {
        if (student.campuses && student.campuses[0]){
          dispatch(delStudentFromCampus(student.campuses[0], student));
        }
        dispatch(delStudent(student.id));
        if (history.location.pathname.indexOf('/campuses/') < 0){
          history.push('/students/');
        }
      })
      .catch(errorMessage => console.error('Error: ', errorMessage));
  };
};

export const updateStudentThunk = (studentData, history) => {
  return dispatch => {
    axios.put(`/api/students/${studentData.id}`, studentData)
      .then(res => res.data)
      .then(updatedStudent => {
        // student.campuses = [campus];
        dispatch(updateStudent(updatedStudent));
        //dispatch(removeStudentFromCampus(student.campuses[0], student));
        //dispatch(addStudentToCampus(campus, student));
        history.push(`/students/${student.id}`);
      })
      .catch(err => console.error('Error: ', errorMessage));
  };
};
