import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Root from './components/root'
// import Home from './components/Home'
import Students from './components/Students'
import CampusProfile from './components/CampusProfile'
import StudentProfile from './components/StudentProfile'
import CampusAdd from './components/CampusAdd'
import StudentAdd from './components/StudentAdd';
import NotFound from './components/NotFound'
class Routes extends Component {

  render () {
    return (
      <Router>
          <Switch>
            <Route exact path="/" component={Root} />
            <Route exact path="/campuses" component={Root} />
            <Route path="/campuses/add" component={CampusAdd} />
            <Route path="/campuses/:id" component={CampusProfile} />
            <Route exact path="/students" component={Students} />
            <Route path="/students/add" component={StudentAdd} />
            <Route path="/students/:id" component={StudentProfile} />
            <Route component={NotFound} />
          </Switch>
      </Router>
    );
  }

}

export default (Routes)
