import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import { delCampusThunk } from '../reducers/campuses';

export class Campus extends Component {
  constructor(props) {
    super(props);

    this.handleRemove = this.handleRemove.bind(this);
  }

  // Handler for clicking X Button next to campus names -> calls thunk
  handleRemove(event) {
    event.stopPropagation();
    this.props.delCampus(this.props.campus.id);
  }

  render () {
    const { campus } = this.props;
    return (

    <div className="campus-img">
  <Link to='/campuses/add' ><button className='btn btn-light addButton'> Add Campus </button></Link>
        <img src={campus.imageUrl} width="50%" />
        <div className="campus-name">
          <NavLink to={`/campuses/${campus.id}`}
            className="btn"
            role="button"><h3>{campus.name}</h3>
          </NavLink>
          <button
            onClick={this.handleRemove}
            className="btn-remove">Delete
          </button>
        </div>

      </div>

    );
  }
}

const mapDispatchToProps = (dispatch, test) => {
  let { history } = test;
  return {
    delCampus: campusId => {
      dispatch(delCampusThunk(campusId, history));
    }
  };
};

const connector = connect(null, mapDispatchToProps);
export default connector(Campus);
