import React, { Component } from 'react';
import { connect } from 'react-redux';
import Campus from './Campus';
import Header from './Header'
import Student from './Student';
import { updateStudentThunk } from '../reducers/students'

class StudentProfile extends Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit (event) {
    event.preventDefault();
    const {chosenStudent} = this.props;
    chosenStudent.firstName = event.target.studentFirstName.value,
    chosenStudent.lastName = event.target.studentLastName.value,
      // campusId: 1,
      chosenStudent.imageUrl = event.target.studentImageUrl.value,
      chosenStudent.email = event.target.studentEmail.value,
      chosenStudent.gpa = event.target.studentGpa.value,
    this.props.updateStudent(chosenStudent);
  }

  render () {
    const { chosenStudent, history } = this.props;
    const singleStudent = (<Student
      student={chosenStudent}
      history={history} />);

    return (
      <div>
          <div className="heading">
          <Header />
          <h3 className="title">Student</h3>
            <div className="all-container">
              {singleStudent}
            </div>
            <div className="img-container">
        <p>E-mail: {chosenStudent.email}</p>
        <p>GPA: { chosenStudent.gpa}</p>
        <p><img src={chosenStudent.imageUrl} /></p>
        </div>
        </div>
        <div><div>
          <div className="heading">
            <h1 className="title">Edit Student</h1>
          </div>
          <div className="body">
            <div className="all-container">
              <div className="update">
                <form onSubmit={this.handleSubmit}>
                  <div className="form-group">
                    <label>First Name</label>
                    <input
                      className="form-input"
                      type="text"
                      name="studentFirstName"
                      defaultValue={chosenStudent.firstName} />
                    <div><label>Last Name</label>
                    <input
                      className="form-input"
                      type="text"
                      name="studentLastName"
                      defaultValue={chosenStudent.lastName} />
                      </div>
                    <div>
                      <label>Email</label>
                      <input
                        className="form-input"
                        type="email"
                        name="studentEmail"
                        defaultValue={chosenStudent.email} />

                    </div>
                    <div>
                    <label>GPA</label>
                    <input
                      className="form-input"
                      type="number"
                      name="studentGpa"
                      defaultValue={chosenStudent.gpa} />

                    </div>
                    <div>
                    <label>Image URL</label>
                    <input
                      className="form-input"
                      type="text"
                      name="studentImageUrl"
                      defaultValue={chosenStudent.imageUrl} />
                    </div>
                  </div>
                  <div className="form-group">
                    <button type="submit" className="btn">
                      Update
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
            <h3 className="title">Campus</h3>
            <div className="students-container">
              {
                (chosenStudent.campuses.length > 0) ?
                  <Campus campus={chosenStudent.campuses[0]} history={history} /> :
                  <div>No Campus Available</div>
              }
            </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  const studentId = +props.match.params.id;
  const chosenStudent = state.students.find(checkStudent => {
    return checkStudent.id === studentId;
  });
  const campuses = state.campuses;
  const { history } = props;
  return { campuses, chosenStudent, history,  };
};

const mapDispatchToProps = (dispatch, props) => {
  let { history } = props;
  return {
    updateStudent: student => {
      dispatch(updateStudentThunk(student, history));
    }
  };
};

const connector = connect(mapStateToProps, mapDispatchToProps);
export default connector(StudentProfile);
