import React, { Component } from 'react';
import { connect } from 'react-redux';
import Campus from './Campus';
import Student from './Student';
import Header from './Header'
import { updateCampusThunk } from '../reducers/campuses';
class CampusProfile extends Component {
  constructor (props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // Edits campus based on form data then calls thunk
  handleSubmit (event) {
    event.preventDefault();
    const campus = {
      id: this.props.campus.id,
      name: event.target.campusName.value,
      address: event.target.campusAddress.value,
      imageUrl: event.target.campusImageUrl.value,
      description: event.target.campusDescription.value
    };
    this.props.updateCampus(campus);
  }

  render () {
    const { students, campus, history } = this.props;

    const singleCampus = (<Campus
      campus={campus}
      history={history} />);

    const studentsAtCampus = (students && students.length) ?
      students.map(student => (<Student
        key={student.id}
        student={student}
        campus={campus}
        history={history} />)) :
      undefined;

    return (
        <div>
          <div className="heading">
          <Header />
            <h3 className="title">Campus</h3>
          </div>
          <div>
            <div className="all-container">
              {singleCampus}
              <div className="campus-address">
              <p>{campus.address}</p>
            </div>
            <div className="campus-desc">
              {campus.description}
            </div>
            <div className="container">
        <div>
          <div className="heading">
            <div className="title"><strong>Edit Campus</strong></div>
          </div>
          <div className="body">
            <div className="all-container">
              <div className="update">
                <form onSubmit={this.handleSubmit}>
                  <div className="form-group">
                    <label>Name</label>
                    <input
                      className="form-input"
                      type="text"
                      name="campusName"
                      defaultValue={campus.name} />
                    <div>
                      <label>Address</label>
                    <input
                      className="form-input"
                      type="text"
                      name="campusAddress"
                      defaultValue={campus.address} />
                      </div>
                    <div>
                    <label>Image URL</label>
                    <input
                      className="form-input"
                      type="text"
                      name="campusImageUrl"
                      defaultValue={campus.imageUrl} />
                      </div>
                      <div>
                    <label>Description</label>
                    <textarea
                      className="form-input campus-desc-control"
                      type="text"
                      name="campusDescription"
                      defaultValue={campus.description} />
                      </div>
                  </div>
                  <div className="form-group">
                    <button type="submit" className="btn">
                      Update
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
          </div>
        </div>
        <div>
          <div>
            <h3 className="title">Students</h3>
          </div>
          <div>
            { !studentsAtCampus && <div>No Students Available</div> }
              {studentsAtCampus}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  const campusId = +props.match.params.id;
  const campus = state.campuses.find(checkCampus => {
    return checkCampus.id === campusId;
  });
  let students = [];
  if (campus) {
    students = campus.students;
    students.forEach(student => {
      student.campuses = [campus];
    });
  }
  const { history } = props;
  return { campus, students, history };
};

const mapDispatchToProps = (dispatch, props) => {
  let { history } = props;
  return {
    updateCampus: (campus) => {
      dispatch(updateCampusThunk(campus, history));
    },
  }
};

const connector = connect(mapStateToProps, mapDispatchToProps);
export default connector(CampusProfile);
