import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addCampusThunk } from '../reducers/campuses';
import Header from './Header'

class CampusAdd extends Component {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // Creates campus based on form data then adds using thunk
  handleSubmit (event) {
    event.preventDefault();
    const campus = {
      name: event.target.campusName.value,
      address: event.target.campusAddress.value,
      imageUrl: event.target.campusImageUrl.value,
      description: event.target.campusDescription.value
    };
    this.props.addCampus(campus);
  }

  render () {
    return (
      <div className="container-add-pages">
              <Header />
              <p></p>
        <div>
          <div className="heading">
            <h3 className="title">New Campus</h3>
          </div>
          <div className="body">
            <div className="all-container">
              <div className="update">
                <form onSubmit={this.handleSubmit}>
                  <div className="form-group">
                    <label>Name</label>
                    <input
                      className="form-control"
                      type="text"
                      name="campusName"
                      placeholder="Enter Campus Name" />
                    <label>Address</label>
                    <input
                      className="form-control"
                      type="text"
                      name="campusAddress"
                      placeholder="Enter Address" />
                    <label>Image URL</label>
                    <input
                      className="form-control"
                      type="text"
                      name="campusImageUrl"
                      defaultValue="img/mit.png" />
                    <label>Description</label>
                    <textarea
                      className="form-control campus-desc-control"
                      type="text"
                      name="campusDescription"
                      placeholder="Enter Description" />
                  </div>
                  <div className="form-group">
                    <button type="submit" className="btn">
                      Submit
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch, props) => {
  let { history } = props;
  return {
    addCampus: (campus) => {
      dispatch(addCampusThunk(campus, history));
    },
  }
};

const connector = connect(null, mapDispatchToProps);
export default connector(CampusAdd);
